
.new-info-box {
  margin-top: 5px;
  width: 90%;
  margin-left: 7.5%;

  .comment-top {
    display: flex;

    .user-box {
      flex: 1;
      display: flex;
      align-items: center;
      position: relative;
      height: 30px;

      .name {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #777888;
        margin-right: 20px;
      }

      .date {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #777888;
        // margin-right: 50px;
      }
    }

    .opt-comment {
      display: flex;
      align-items: center;
      font-size: 16px;
      position: relative;

      &:hover .dropdown-menu {
        display: block;
        z-index: 10;
      }

      .dropdown-menu {
        border-radius: 4px;
        box-shadow: 0 3px 8px 1px rgba(29, 20, 5, 0.22);
        position: absolute;
        background-color: #fff;
        left: -26px;
        top: 100%;
        display: none;

        /*&::before {
                    content: "";
                    position: absolute;
                    top: -20px;
                    left: 50%px;
                    background-color: #000;
                    transform: translateX(-50%);
                    border-bottom: 10px solid white;
                    border-right: 10px solid transparent;
                    border-top: 10px solid transparent;
                    border-left: 10px solid transparent;
                }*/
      }
    }

    .comment-like {
      display: flex;
      cursor: pointer;
      color: #4d4d4d;
      // text-align: right;
      // height: 20px;
      line-height: 24px;
      // margin-top: 2px;
      align-items: center;

      img.comment-like-img {
        width: 16px;
        height: 16px;
        display: none;
      }
      img.unclickImg {
        display: inline-block;
      }

      img.comment-like-img:hover {
        & ~ .comment-like-img-hover {
          display: inline-block;
        }
        display: none;
      }
    }
  }

  .comment-center .new-comment {
    font-size: 14px;
    color: #222226;
    line-height: 22px;
    word-break: break-word;
  }
}
