
.header {
  width: 80%;
  min-width: 1000px;
  max-width: 1200px;
  height: 100px;
  margin: 0 auto;
  display: flex;
  justify-content: left;
  align-items: center;

  .header-left {
    flex: 2;

    // height: 100%;
    img {
      // width: 200px;
      // height: 100%;
    }
  }

  .navbar-collapse {
    flex: 3;
    min-width: 500px;
  }

  .header-right {
    flex: 2;

    .ac-button {
      float: right;
    }
  }
}

.infinite-list {
  height: 100%;
  width: 100%;
  border: 1px solid red;

  li {
    height: 50px;
    background: #e8f3fe;
    margin: 10px;
    color: #7dbcfc;
    text-align: center;
    line-height: 50px;
  }

  .text {
    color: green;
    text-align: center;
    line-height: 50px;
  }
}
