
.box {
    height: 200px;
    display: flex;
    transition: 1s;
    background: linear-gradient(to right, #ebebeb 0, transparent 100%);
    &:hover {
        background: linear-gradient(to left, #ebebeb 0, transparent 100%);
    }
  }

  .box > div {
    height: 100%;
  }

  .box-left {
    width: 200px;
    img {
            width: 100%;
            height: 80%;
            transition: 1s;
            margin: 10% 0;
            &:hover {
                transform:scale(1.1);
            }
    }
  }

  .box-right {

    flex: 1; // 设置flex-grow属性为1，默认为0
    overflow: hidden;
    // background-color: red;
    position: relative;
    padding-left: 20px;
    .box-right-footer {
        // background-color: blue;
        display: flex;
        width: 95%;
        position: absolute;
        bottom: 10%;
        .left {
            flex: 2;
            display: flex;
            justify-content: flex-start;
        }
        //.center {
        //    flex: 1;
        //}
        .right {
            flex: 2;
            display: flex;
            justify-content: flex-end;
        }
    }
  }
