
.register {
  margin: 0px;
  padding: 0px;
  width: 100%;
  //height: 100%;
  box-sizing: border-box;

  // background-color: #f2f2f2;
  background: #e6e6e6;
  background-size: 100% 130%;

  // 注册头部
  .reg_header {
    width: 1210px;
    height: 90px;
    margin: 0 auto;

    &::before {
      content: "";
      height: 100%;
      display: inline-block;
      vertical-align: middle;
    }

    .logo_bg {
      display: inline-block;
      vertical-align: middle;
      width: 321px;
      height: 90px;
      background: url(./images/logo.png);
      background-size: 100% 100%;
    }
  }

  // 注册表单
  .register {
    width: 100%;
    height: 662px;

    .wx-row {
      height: 100%;
      width: 1210px;
      margin: 0 auto;
      background-color: rgba(255, 255, 255);
      box-shadow: 0 6px 20px 0 rgb(0 0 0 / 7%);
      border-radius: 5px;

      .reg_content {
        margin-top: 14px;
        width: 100%;
        height: 100%;

        .title {
          width: 100%;
          height: 83px;
          border-bottom: 1px solid #efefef;
          font-size: 20px;
          line-height: 83px;
          text-align: center;
        }

        .process {
          width: 700px;
          margin: 40px auto 80px;

          .process-1 {
            height: 21px;
            width: 230px;
            position: relative;
            text-align: center;
            display: inline-block;

            &::before {
              content: "";
              display: inline-block;
              width: 230px;
              height: 1px;
              background: #e8e8e8;
              position: absolute;
              bottom: -14px;
              left: 50%;
            }

            &::after {
              content: "";
              width: 8px;
              height: 8px;
              background: #e8e8e8;
              border-radius: 16px;
              position: absolute;
              bottom: -24px;
              left: 50%;
              transform: translateX(-50%);
              border: 8px solid #fff;
            }
          }

          .process-2 {
            height: 21px;
            width: 230px;
            position: relative;
            text-align: center;
            display: inline-block;

            &::after {
              content: "";
              width: 8px;
              height: 8px;
              background: #e8e8e8;
              border-radius: 16px;
              position: absolute;
              bottom: -24px;
              left: 50%;
              transform: translateX(-50%);
              border: 8px solid #fff;
            }
          }
        }

        form {
          width: 520px;
          margin: 0 auto;
          padding-right: 60px;

          // line-height: 42px;
          // color: #333;
          // font-size: 14px;
          .form-item {
            width: 100%;
            height: 42px;
            line-height: 42px;
            margin-bottom: 32px;
            display: flex;
            position: relative;
            box-sizing: border-box;

            .test {
              width: 100px;
              text-align: right;
              margin-right: 38px;
            }
          }

          .form-item-1 {
            input {
              height: 42px;
              width: 322px;
              padding: 13px 11px;
              border: 1px solid #e6e6e6;
              box-sizing: border-box;
            }

            span {
              position: absolute;
              top: 46px;
              font-size: 12px;
              left: 140px;
              line-height: 14px;
              color: #e50834;
            }
          }

          .form-item-3 {
            input {
              height: 42px;
              width: 194px;
              padding: 13px 11px;
              border: 1px solid #e6e6e6;
              box-sizing: border-box;
            }

            button {
              margin-left: 10px;
              width: 118px;
              height: 100%;
              // background: #f5f5f5;
              background-image: linear-gradient(90deg, #c99748, #cf840c);
              font-weight: bold;
              border: 1px solid #e6e6e6;
              border-radius: 3px;
            }

            span {
              position: absolute;
              top: 46px;
              font-size: 12px;
              left: 140px;
              line-height: 14px;
              color: #e50834;
            }
          }

          .form-item-4 {
            width: 100%;
            height: 48px;
            line-height: 48px;
            position: relative;
            box-sizing: border-box;
            font-size: 14px;

            input {
              margin-left: 30px;
              margin-right: 8px;
            }
          }

          .form-item-5 {
            button {
              height: 42px;
              width: 322px;
              padding: 13px 11px;
              border: 1px solid #e6e6e6;
              color: black;
              font-weight: bold;
              // background: #e5e5e5;
              background-image: linear-gradient(90deg, #c99748, #cf840c);
            }
          }
        }

        .success {
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }
    }
  }

  // 注册底部
  .reg_footer {
    width: 100%;

    .content {
      padding: 94px 0 49px;
      text-align: center;
      color: #666;
      font-size: 13px;
      line-height: 25px;
      text-decoration: none;

      p>a {
        text-decoration: none;
        color: #212529;
      }
    }
  }
}