
.editor {
  padding-top: 100px;
  height: calc(100vh - 100px);
  width: 100%;
  overflow: scroll;
  border: 1px solid #f5f6f7;
  background-color: #f5f6f7;

  .editor-up {
    display: flex;
    justify-content: center;
    // width: 91%;
    min-width: 1380px;
    margin: 0 auto;
  }

  .editor-down {
    width: 70%;
    min-width: 816px;
    min-height: 800px;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 30px;
    padding: 20px;

    input {
      width: 98%;
      display: block;
      margin: 0 auto;
      font-size: 40px;
      border: none;
      outline: none;
      font-weight: bold;
    }
  }

  .editor-down-down {
    width: 70%;
    min-width: 816px;
    min-height: 300px;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 30px;
    padding: 20px;
  }

  .editor-down-down.detail {
    .ant-list-item {
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }

    .detail-left {
      width: 200px;
      text-align: right;
      margin-right: 15px;
      font-size: 20px;
      font-weight: bold;
    }

    .detail-right {
      width: 400px;
      .avatar {
        width: 100%;
        height: 100%;
      }
    }
  }

  .editor-submit {
    width: 100%;
    height: 64px;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    border-top: 1px solid #ddd;

    .submit {
      width: 600px;
      height: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
  }
}
