
.user-info {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;

  .userinfo-item {
    height: 50px;
    display: flex;
    align-items: center;

    .label {
      display: inline-block;
      width: 100px;
      text-align: right;
    }
  }

  .avatar {
    //height: 80px;
    width: 180px;
    position: absolute;
    right: 100px;
    top: 0px;
    margin-bottom: 20px;
  }

  .modify {
    position: absolute;
    right: 100px;
    bottom: 60px;

    /*.el-button {
            width: 150px;
            font-size: 20px;
        }*/
  }
}

.replace {
  display: block;
  font-size: 28px;
  color: #fff;
  padding: 10px 10px;
  border: 1px solid #fff;
  border-radius: 30px;
  margin-top: 20px;
}

.hiddenInput {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
}
