
.infinite-list {
    padding-top: 100px;
    height: calc(100vh - 100px);
    width: 100%;
    border: 1px solid #f5f6f7;
    background-color: #f5f6f7;

    .lifestyle {
        background: #f1f8ff;
        padding: 40px 0;
        text-align: center;
        h2 {
          color: #203656;
          font-family: "Poppins", sans-serif;
          font-weight: 700;
          line-height: 1.4;
          margin: 20px 0;
        }
        span {
          font-size: 14px;
          color: #9faabb;
        }
        .searchk {
          width: 60%;
          margin: 0 auto;
          min-width: 800px;
          max-width: 1000px;
        }
      }

    .box-card {
        width: 80%;
        min-width: 1000px;
        max-width: 1200px;
        margin: 0 auto;

        .select-type {
            margin-right: 20px;
        }
    }


}

.text {
    color: green;
    text-align: center;
    line-height: 50px;
}
