
.infinite-list {
    padding-top: 100px;
    height: calc(100vh - 100px);
    width: 100%;
    border: 1px solid #f5f6f7;
    background-color: #f5f6f7;

    .box-card {
        width: 80%;
        min-width: 1000px;
        max-width: 1200px;
        margin: 0 auto;
        margin-top: 20px;
        position: relative;

        .input {
            position: absolute;
            top: 20px;
            right: 20px;
            width: 130px;
            height: 60px;
            box-sizing: border-box;
            border: 2px solid #ccc;
            border-radius: 4px;
            font-size: 20px;
            background: #f5f6f7;
            // padding: 12px 20px 12px 40px;
            -webkit-transition: width 0.4s ease-in-out;
            transition: width 0.4s ease-in-out;
        }

        .input:focus {
            width: calc(100% - 40px);
            outline-color: #409eff;
        }

        .userinfo {
            padding-left: 120px;
        }

        .select-type {
            margin-right: 20px;
        }
    }
}

.text {
    color: green;
    text-align: center;
    line-height: 50px;
}
