
.detail {
  padding-top: 100px;
  height: calc(100vh - 100px);
  width: 100%;
  border: 1px solid #f5f6f7;
  background-color: #f5f6f7;

  .title-bg {
    width: 100%;
    height: 250px;
    background-color: #79869a;
    padding-top: 200px;

    .title {
      width: 80%;
      // height: 200px;
      margin: 0 auto;
      min-width: 1000px;
      max-width: 1200px;
    }
  }
  .box-card {
    width: 80%;
    min-width: 1000px;
    max-width: 1200px;
    margin: 0 auto;
    margin-top: 200px;
  }
  .box-card.down {
    margin-top: 0px;
    position: sticky;
    bottom: 0;
    display: flex;
    justify-content: flex-end;
    .button {
      margin-right: 20px;
    }
  }
}
