
.infinite-list {
    padding-top: 100px;
    height: calc(100vh - 100px);
    width: 100%;
    border: 1px solid #f5f6f7;
    background-color: #f5f6f7;

    .box-card {
        width: 80%;
        min-width: 1000px;
        max-width: 1200px;
        margin: 0 auto;

        .select-type {
            margin-right: 20px;
        }
    }


}

.text {
    color: green;
    text-align: center;
    line-height: 50px;
}
