@import "./common/var.scss";
@import "./mixins/mixin.scss";

@include b(button-group){
    & > .#{$namespace}-button {
       &:first-child {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
       }
       &:last-child {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
       }
    }
}
