
.attentionfanitem {
    display: flex;
    justify-content: flex-start;
    border-bottom: 1px solid #f0f0f2;
    align-items: center;
    .attentionfanitem-avatar {
        width: 60px;
        height: 60px;
        background-color: yellow;
        margin-right: 20px;
        border-radius: 30px;
    }
    .attentionfanitem-btn {
        position: absolute;
        right: 20px;
    }
}
