
.el-upload {
  color: #d9d9d9;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 178px;
  height: 178px;
  text-align: center;
  line-height: 178px;
  // display: inline-block;

  .el-upload__input {
    display: none;
  }
}

.el-upload:hover {
  border-color: #409eff;
  color: #409eff;
}
